body {
    color: @text-color;
    font-family: 'Open sans',Arial, Helvetica, sans-serif;
    min-width: 320px;
  }

  a {
    text-decoration: none;
  }

  img {
      max-width: 100%;
  }

  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }

  .visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
